h2 {
    font-size: 30px;
    text-align: center;
    color:red;
}
.watch button {
    font-size: 20px;
    padding: 10px 15px;
    margin: 5px;
    border: none;
    border-radius: 5px;
    background-color: #1605ff;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;
}
.watch button:hover {
    background-color: #45a049;
}
.buttons {
    text-align: center;
    margin-top: 20px;
}
.laps {
    margin: 20px auto;
    width: 80%;
    text-align: left;
    background-color: #000000;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    height: 600px;
}
.laps h3 {
    font-size: 1.5rem;
    color: #ffffff;
    border-bottom: 2px solid #4caf50;
    padding-bottom: 5px;
}
.laps ul {
    list-style: none;
    padding: 0;
    margin-top: 280px;
    left: 43%;
}
.laps ul li {
    font-size: 1.2rem;
    color: #ffffff;
    padding: 5px 0;
    border-bottom: 1px solid #ddd;
}
.laps ul li:last-child {
    border-bottom: none;
}
